import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPlay,
  faAngleLeft,
  faAngleRight,
  faPause,
  faVolumeDown,
  faVolumeUp,
  faVolumeMute,
} from "@fortawesome/free-solid-svg-icons";

//styles
import "./styles/_player.scss";

// eslint-disable-next-line import/no-anonymous-default-export
export default ({
  songs,
  setSongs,
  currentSong,
  setCurrentSong,
  timeUpdateHandler,
  songInfo,
  setSongInfo,
  audioRef,
  isPlaying,
  setIsPlaying,
}) => {
  const [volume, setVolume] = useState(0.5);

  const activeLibraryHandler = (nextPrev) => {
    const newSongs = () => {
      return songs.map((newSong) => {
        if (newSong.id === nextPrev.id) {
          newSong.active = true;
          return newSong;
        } else {
          newSong.active = false;
          return newSong;
        }
      });
    };
    setSongs(newSongs());
  };

  const playSongHandler = () => {
    if (isPlaying) {
      audioRef.current.pause();
      setIsPlaying(false);
    } else {
      audioRef.current.play();
      setIsPlaying(true);
    }
  };


  const getTime = (time) => {
    return (
      Math.floor(time / 60) + ":" + ("0" + Math.floor(time % 60)).slice(-2)
    );
  };

  const dragHandler = ({ target }) => {
    audioRef.current.currentTime = target.value;
    setSongInfo({ ...songInfo, currentTime: target.value });
  };

  const skipTrackHandler = async (direction) => {
    const currentSongX = songs.findIndex((song) => song.id === currentSong.id);

    if (direction === "skip-back") {
      if ((currentSongX - 1) % songs.length === -1) {
        await setCurrentSong(songs[songs.length - 1]);
        activeLibraryHandler(songs[songs.length - 1]);
        audioRef.current.play();
        return;
      }
      await setCurrentSong(songs[(currentSongX - 1) % songs.length]);
      activeLibraryHandler(songs[(currentSongX - 1) % songs.length]);
    }
    if (direction === "skip-forward") {
      await setCurrentSong(songs[(currentSongX + 1) % songs.length]);
      activeLibraryHandler(songs[(currentSongX + 1) % songs.length]);
    }

    setIsPlaying(true);
    audioRef.current.play();
  };

  const stylesTrackAnim = {
    transform: `translateX(${songInfo.animationPercantage}%)`,
  };
  const stylesVolumeInput = {
    transform: `translateX(${volume * 100}%)`,
  };
  const stylesTrackGradient = {
    background: `linear-gradient(to right, ${currentSong.color[1]}, ${currentSong.color[0]})`,
  };

  return (
    <div className="player">
      <div className="time-control">
        <p>{getTime(songInfo.currentTime)}</p>
        <div className="track" style={stylesTrackGradient}>
          <input
            min={0}
            max={songInfo.duration || 0}
            value={songInfo.currentTime}
            onChange={dragHandler}
            type="range"
          />
          <div style={stylesTrackAnim} className="animate-track"></div>
        </div>
        <p>{songInfo.duration ? getTime(songInfo.duration) : "0:00"}</p>
      </div>
      <div className="play-control">
        <FontAwesomeIcon
          onClick={() => skipTrackHandler("skip-back")}
          className="skip-back"
          size="2x"
          icon={faAngleLeft}
        />
        <FontAwesomeIcon
          className="play"
          onClick={playSongHandler}
          size="2x"
          icon={isPlaying ? faPause : faPlay}
        />
        <FontAwesomeIcon
          onClick={() => skipTrackHandler("skip-forward")}
          className="skip-forward"
          size="2x"
          icon={faAngleRight}
        />
      </div>
      <div className="volume-control">
        <div className="volume-icons">
          {volume > 0.5 && <FontAwesomeIcon size="1x" icon={faVolumeUp} />}
          {volume <= 0.5 && volume > 0.01 && (
            <FontAwesomeIcon size="1x" icon={faVolumeDown} />
          )}
          {volume < 0.01 && <FontAwesomeIcon size="1x" icon={faVolumeMute} />}
        </div>
        <div className="track mn-h" style={stylesTrackGradient}>
          <input
            type="range"
            className="volume-range"
            min={0}
            max={1}
            step={0.01}
            onChange={(e) => {
              setVolume(e.target.value);
              audioRef.current.volume = volume;
            }}
            value={volume}
          />
          <div className="animate-track" style={stylesVolumeInput}></div>
        </div>
      </div>

      <audio
        onTimeUpdate={timeUpdateHandler}
        onLoadedMetadata={timeUpdateHandler}
        ref={audioRef}
        src={currentSong.audio}
        onEnded={() => skipTrackHandler("skip-forward")}
      ></audio>
    </div>
  );
};
